S;EACX;AACF","sourcesContent":["@font-face {\n  font-family: 'NK Slavjanica';\n  font-weight: normal;\n  src: url('../fonts/nk_slavj.ttf') format('truetype'), url('../fonts/nk_slavj.woff') format('woff');\n}\n\n@font-face {\n  font-family: 'Alfios';\n  font-weight: normal;\n  src: url('../fonts/Alfios_R.ttf') format('truetype'), url('../fonts/Alfios_R.otf') format('opentype');\n}\n\n@font-face {\n  font-family: 'Alfios';\n  font-weight: bold;\n  src: url('../fonts/Alfios_B.ttf') format('truetype'), url('../fonts/Alfios_B.otf') format('opentype');\n}\n\n* {\n  box-sizing: border-box;\n  padding: 0;\n  margin: 0;\n}\n\n:root {\n  --default-font-size: 10px;\n  --primary-color: #d3d3d3;\n\n  --header-font: 4.5rem 'NK Slavjanica', serif;\n  --main-font: 1rem 'Alfios', serif;\n\n  --ancient-card-shadow: inset 0.15rem 0.15rem 0.3rem black, inset -0.15rem -0.15rem 0.3rem black;\n  --mythos-card-shadow: 0.15rem 0.15rem 0.3rem black;\n\n  --secondary-color: rgba(211, 211, 211, 0.8);\n  --tertiary-color: #c7834e;\n\n  --primary-transform: scale(1.1);\n  --primary-transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out;\n  --secondary-transition: background-image 0.3s ease-in-out, width 0.3s ease-in-out, height 0.3s ease-in-out,\n    color 0.3s ease-in-out, opacity 0.3s ease-in-out;\n  --card-transition: background-image 0.15s ease-in-out;\n  --card-wrapper-transition: all 0.2s ease-in-out;\n\n  --inactive-opacity: 0.5;\n}\n\nhtml {\n  overflow-x: hidden;\n  min-height: 100vh;\n\n  font-size: var(--default-font-size);\n}\n\nbody {\n  background: url(../img/home.png) no-repeat center/cover;\n}\n\n.wrapper {\n  display: flex;\n  flex-direction: column;\n\n  max-width: 156rem;\n  min-height: 100vh;\n  padding: 1rem 3rem;\n  margin: 0 auto;\n}\n\n.button::after {\n  position: absolute;\n  width: 0;\n  height: 0;\n  overflow: hidden;\n  z-index: -1;\n\n  content: url(../img/controls/shuffle-button-clicked.png) url(../img/controls/button-clicked.png);\n}\n\n/* Header Styles */\n.header {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n\n.header__name {\n  font: var(--header-font);\n  color: var(--primary-color);\n}\n\n.header__description {\n  font: var(--main-font);\n  font-size: 2.2rem;\n  color: var(--primary-color);\n  opacity: 0.7;\n}\n\n/* Main Styles */\n.main {\n  flex: 1;\n\n  display: flex;\n  padding-top: 1rem;\n}\n\n/* Ancient Styles */\n.ancients {\n  flex: 1 0 40%;\n\n  display: flex;\n  flex-wrap: wrap;\n  align-content: center;\n  column-gap: 4rem;\n  row-gap: 2vh;\n}\n\n.ancients__card__wrapper {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n\n  height: 28rem;\n  width: 22rem;\n\n  padding: 0.7rem;\n  padding-left: 1rem;\n\n  background: url(../img/controls/card-tile.png) no-repeat center;\n  background-size: 100% 100%;\n\n  transition: var(--primary-transition);\n}\n\n.ancients__card__wrapper.inactive {\n  opacity: var(--inactive-opacity);\n}\n\n.ancients__card__wrapper:hover {\n  cursor: pointer;\n  transform: var(--primary-transform);\n}\n\n.ancients__card__wrapper.inactive:hover {\n  opacity: 1;\n}\n\n.ancients__card {\n  height: 100%;\n  width: 100%;\n\n  border-radius: 1.5rem;\n  box-shadow: var(--ancient-card-shadow);\n\n  background-repeat: no-repeat;\n  background-position: top;\n  background-size: cover;\n}\n\n/* Complexities Styles */\n\n.complexities {\n  flex: 1 0 20%;\n\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  gap: 2rem;\n\n  padding-top: 2rem;\n  padding-bottom: 7rem;\n}\n\n.complexities__button {\n  height: 4rem;\n  width: 22rem;\n  padding: 1.2rem 5rem;\n\n  border: none;\n  outline: none;\n\n  font-family: 'Alfios';\n  font-size: 1.7rem;\n  line-height: 1.9rem;\n  text-align: center;\n  color: var(--secondary-color);\n\n  background: url(../img/controls/button.png) no-repeat center/cover;\n\n  transition: var(--secondary-transition);\n}\n\n.complexities__button.inactive {\n  opacity: var(--inactive-opacity);\n}\n\n.complexities__button:hover,\n.complexities__button.active {\n  cursor: pointer;\n  color: var(--tertiary-color);\n  background-image: url(../img/controls/button-clicked.png);\n}\n\n.complexities__button.inactive:hover {\n  opacity: 1;\n}\n\n/* Deck Styles */\n\n.shuffled-deck {\n  flex: 1 0 40%;\n\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n}\n\n.shuffled-deck__wrapper {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  gap: 2rem;\n}\n\n/* Stage Counters Styles */\n\n.stage {\n  display: flex;\n  align-items: center;\n  padding-right: 4rem;\n}\n\n.stage__header {\n  flex: 1 0 40%;\n\n  padding-right: 2rem;\n\n  color: var(--tertiary-color);\n  font-family: 'Alfios';\n  font-weight: bold;\n  font-size: 2rem;\n  text-align: right;\n}\n\n.stage__cards-counters {\n  display: flex;\n  gap: 2rem;\n}\n\n.stage__cards-counters__color-counter {\n  height: 4rem;\n  width: 4rem;\n\n  font-family: 'NK Slavjanica';\n  font-size: 2rem;\n  font-weight: bold;\n  color: var(--tertiary-color);\n\n  padding: 0.65rem 1.3rem;\n  text-align: center;\n}\n\n.stage__cards-counters__color-counter.green {\n  background: url(../img/controls/green-counter.png) no-repeat center/cover;\n}\n\n.stage__cards-counters__color-counter.brown {\n  background: url(../img/controls/brown-counter.png) no-repeat center/cover;\n}\n\n.stage__cards-counters__color-counter.blue {\n  background: url(../img/controls/blue-counter.png) no-repeat center/cover;\n}\n\n/* Deck Shuffle Styles */\n\n.shuffle-deck-button {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n\n  height: 4rem;\n  width: 40%;\n\n  border: none;\n  outline: none;\n\n  margin: 2rem 0rem;\n\n  font-family: 'Alfios';\n  font-size: 1.7rem;\n  color: var(--tertiary-color);\n\n  background: url(../img/controls/shuffle-button.png) no-repeat center;\n  background-size: 100% 100%;\n\n  transition: var(--secondary-transition);\n}\n\n.shuffle-deck-button.inactive {\n  opacity: var(--inactive-opacity);\n}\n\n.shuffle-deck-button:hover {\n  cursor: pointer;\n  background-image: url(../img/controls/shuffle-button-clicked.png);\n}\n\n.shuffle-deck-button.active {\n  cursor: auto;\n  background-image: url(../img/controls/shuffle-button-clicked.png);\n  opacity: var(--inactive-opacity);\n}\n\n.shuffle-deck-button.inactive:hover {\n  cursor: auto;\n  background-image: url(../img/controls/shuffle-button.png);\n}\n\n.shuffled-deck__cards__wrapper {\n  display: flex;\n  gap: 4rem;\n}\n\n.shuffled-deck__cards__wrapper.inactive {\n  opacity: 0;\n}\n\n.shuffled-deck__cards__wrapper.deck-start,\n.shuffled-deck__cards__wrapper.deck-finish {\n  gap: 0;\n}\n\n.card__wrapper {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n\n  height: 29rem;\n  width: 21rem;\n\n  padding: 0.7rem;\n  padding-left: 1rem;\n\n  background: url(../img/controls/card-tile-no-glow.png) no-repeat center;\n  background-size: 100% 100%;\n}\n\n.card-closed__wrapper:hover {\n  cursor: pointer;\n}\n\n.card-opened__wrapper.deck-start,\n.card-closed__wrapper.deck-finish {\n  padding: 0;\n  width: 0;\n}\n\n.card-closed__wrapper,\n.card-opened__wrapper:not(.deck-start) {\n  transition: var(--card-wrapper-transition);\n}\n\n.card {\n  height: 100%;\n  width: 100%;\n\n  background-repeat: no-repeat;\n  background-position: bottom;\n  background-size: 101% 101%;\n\n  border-radius: 1.5rem;\n  box-shadow: var(--mythos-card-shadow);\n\n  transition: var(--card-transition);\n}\n\n.card-closed {\n  background-image: url(../img/mythosCardBackground.png);\n}\n\n@media (min-width: 1450px) {\n  :root {\n    --default-font-size: 11px;\n  }\n\n  .wrapper {\n    padding: 1rem 5rem;\n  }\n}\n\n@media (min-width: 1600px) {\n  :root {\n    --default-font-size: 12px;\n  }\n}\n\n@media (max-width: 1270px) {\n  :root {\n    --default-font-size: 8px;\n  }\n}\n\n@media (max-width: 1023px) {\n  :root {\n    --default-font-size: 6px;\n  }\n}\n\n@media (max-width: 770px) {\n  :root {\n    --default-font-size: 4px;\n  }\n}\n\n@media (max-width: 650px) {\n  .shuffled-deck__cards__wrapper {\n    flex-direction: column;\n  }\n\n  .wrapper {\n    padding: 1rem 1.5rem;\n  }\n\n  .ancients {\n    flex: 1 0 35%;\n    justify-content: center;\n  }\n\n  .shuffled-deck {\n    flex: 1 0 35%;\n  }\n\n  .card-opened__wrapper.deck-start,\n  .card-closed__wrapper.deck-finish {\n    height: 0;\n  }\n}\n"],"sourceRoot":""}]);
